import reduce from 'immer'
import React, {useState} from 'react'
import '../public/static/output.css'

const HomeList = () => {
    const homes = []
    objects.forEach((obj) => {
        homes.push(<Item title={obj.title} type={obj.type} price={obj.price + obj.costs} rooms={obj.rooms} img={obj.image}/>)
    })
    return (
        <p className="h-full">
            {homes}
        </p>
    )
}

const Item = ({title, type, price, rooms, img}) => {
    const [selected, setSelected] = useState(false);

    if(selected) {
        return (
            <p className="bg-white border-2 border-gray-600 shadow text-xs overflow-hidden text-center"
                onClick={() => {setSelected(false)}}>
                <p className="p-1 float-left">{img}</p>
                {/*<p className="pt-2 float-left text-center text-xs shadow ml-auto -mr-auto">*/}
                    <p className="bg-gray-300"><b>{title}</b></p>
                    <p>{`${type}, ${rooms}`}</p>
                    <p className="text-base"><b>{price}.-</b></p>
                {/*</p>*/}
            </p>
        )
    } else {
        return (
            <p className="h-24 bg-white border-2 border-gray-600 shadow text-xs overflow-hidden text-center p-1"
                onClick={() => {setSelected(true)}}>
                <p className="w-1/2 float-left">{img}</p>
                {/*<p className="w-1/2 pt-2 float-right text-center text-xs shadow">*/}
                    <p className="bg-gray-300"><b>{title}</b></p>
                    <p>{`${type}, ${rooms}`}</p>
                    <p className="text-base"><b>{price}.-</b></p>
                
            </p>
        )
    }
    
}

